  leftClick = (event) => {


      let index = this.state.currentid;
       index = Number(index);
      for (var i = this.state.robotsFil.length - 1; i >= 0; i--) {

        if(this.state.robotsFil[i].id===index&&i!==this.state.robotsFil.length-1){


        index = this.state.robotsFil[(i+1)].id;
        break;


        }
      }
      this.setState({currentid:(index)});
  }

  rightClick = (event) => {



      let index = this.state.currentid;
       index = Number(index);
       if(index>=0){}
      for (var i = this.state.robotsFil.length - 1; i >= 0; i--) {

        if(this.state.robotsFil[i].id===index&&i!==0){


        index = this.state.robotsFil[(i-1)].id;
        break;


        }
      }
      this.setState({currentid:(index)});





  }




  titoloLoaded = () =>{
    this.setState({titoloLoaded:true})
  }

  onSearchChange = (event) =>  {
    this.setState({searchfield: event.target.value})
    this.changeImages(event.target.value, true);
  }

  changeImages = (searchfield, bool) => {
    searchfield = searchfield.toLowerCase();
    let immagini = document.getElementsByClassName("card");
    for(var counter=0; counter<immagini.length;counter++){
      if(/\d/.test(immagini.item(counter).id)){
        if(bool)
          immagini.item(counter).classList.remove("cardDib")


        if(immagini.item(counter).attributes.tag.nodeValue.toLowerCase().includes(searchfield)){
          immagini.item(counter).classList.remove("displayNone")
          immagini.item(counter).classList.add("dib")
        }else{
          //console.log(immagini.item(counter))
          immagini.item(counter).classList.add("displayNone")
          immagini.item(counter).classList.remove("dib")
          }

          if(immagini.item(counter).attributes.alt.nodeValue<0) {
            immagini.item(counter).classList.add("displayNone")
            immagini.item(counter).classList.remove("dib")
          }
          if(
          ((searchfield==="ehyes")||(searchfield==="eh yes"))
          &&
          (immagini.item(counter).attributes.alt.nodeValue<0)
          ){
            immagini.item(counter).classList.remove("displayNone")
            immagini.item(counter).classList.add("dib")
          }

      }
    }


  }

  orientationChange = (event) => {
    if(window.Scrollamelo)
      window.Scrollamelo.scrollTop = 0;
  }

  resizePagina = (event) =>{

    if(window.Scrollamelo)
      window.Scrollamelo.scrollTop = 0;


    if(true){
      if(this.state.type==="desk"){

      this.setState({inWidth:window.innerWidth});
      this.setState({inHeight:window.innerHeight});

      if(window.innerHeight>window.innerWidth){
        this.setState({h:'auto'});
        this.setState({w:'85%'});
        this.setState({check:0});
      }else{
        this.setState({h:300});
        this.setState({w:'auto'});
        this.setState({check:1});
      }

    }else{


        this.setState({inWidth:window.outerWidth});
        this.setState({inHeight:window.outerHeight});

        if(window.screen.height>window.screen.width){
          this.setState({h:'auto'});
          this.setState({w:'85%'});
          this.setState({check:0});
        }else{
          this.setState({h:300});
          this.setState({w:'auto'});
          this.setState({check:1});
        }
      }
    }
  }



