  mostraFormCaricaImmagine = () =>{
    if(window.menu)this.mostraMenu();



      let window1 = document.getElementById(`caricaWindow`);
  		let imgSfondo = document.getElementById(`imgSfondo`);
  		//immagine.classList.toggle("displayNone");
      if(window1.classList.contains('caricaWindowOpenMobile')&&(!this.state.amIOffline)){
  		window1.classList.remove("caricaWindowOpenMobile");
      window1.classList.remove("caricaWindowMinimizedMobile");
      imgSfondo.classList.remove("imgSfondoDrop");}

      if(this.state.amIOffline){

        var h44 = document.createElement("h4");
        h44.innerText="sei offline😱"
        h44.classList.add("h44")

        if(window.titoloTop){
          let titolo = document.getElementById("titoloTop")
          var titoloParent = titolo.parentElement
            if(this.state.amIOffline===true&&(titoloParent.lastElementChild.id==="titoloTop")){
              //if()


              titoloParent.appendChild(h44);
            }else{
              titoloParent.removeChild(titoloParent.lastElementChild);
              titoloParent.appendChild(h44);
            }
        }


      }

  }

  mostraMenu = () =>{


    if(this.state.menuState === null){
      this.setState({menuState:"ciao"});
      window.menu.classList.replace("widthmenunull","widthmenufull")
      window.caricaUl.classList.replace("opacitynullII","opacitytopII")
      window.menuComplementare.classList.add("widthmenuComplementarefull")
      window.versione.classList.replace("opacitynullII","opacitytopII")

    }else{
      this.setState({menuState:null});
      window.menu.classList.replace("widthmenufull","widthmenunull")
      window.caricaUl.classList.replace("opacitytopII","opacitynullII")
      window.menuComplementare.classList.remove("widthmenuComplementarefull")
      window.versione.classList.replace("opacitytopII","opacitynullII")
    }
  }

